  padding-bottom: 12px;
  color: #FFFFFF;
}
.historial__canje .table__container .table-box table tr {
  border-bottom: 2px solid #412EC2;
}
.historial__canje .table__container .table-box.active thead {
  transition: all 0.5s linear;
  background-color: #412EC2;
  border-bottom-color: #00C9B7;
}
.historial__canje .table__container .table-box table,
.historial__canje .table__container .table-box th,
.historial__canje .table__container .table-box td {
  border-collapse: collapse;
