.settings__container {
  margin-top: 5rem;
}
.settings__container h2 {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  color: #FFFFFF;
}
.settings__container .navegacion__setting {
  padding-top: 3rem;
}
.settings__container .navegacion__setting ul {
  display: flex;
  border-bottom: 1px solid #FFFFFF;
  width: 100%;
}
.settings__container .navegacion__setting ul li {
  position: relative;
}
.settings__container .navegacion__setting ul li a {
  position: relative;
  display: inline-block;
  padding: 1rem 2rem;
  font-size: 1.9rem;
  color: #FFFFFF;
  font-weight: 500;
}
.settings__container .navegacion__setting ul li a::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #00C9B7;
  left: 0;
  top: 41px;
  transform: scaleX(0);
  transition: all 0.4s ease;
}
.settings__container .navegacion__setting ul li a:hover::before {
  transform: scaleX(1);
}
.settings__container .settigns__content {
  display: flex;
  align-items: flex-start;
  place-content: flex-start;
  display: flex;
  flex-flow: row wrap;
  padding: 4rem 0;
}
.settings__container .settigns__content h3 {
  flex-basis: 35%;
  color: #FFFFFF;
  font-weight: 500;
  font-family: "Roboto", cursive;
}
.settings__container .settigns__content .settigs-pages {
  flex-basis: 65%;
}
.settings__container .settigns__content .settings1 .campo {
  display: flex;
  flex-direction: column;
}
.settings__container .settigns__content .settings1 .campo label {
  font-size: 2rem;
  color: #FFFFFF;
}
.settings__container .settigns__content .settings1 .campo .info_campo {
  display: flex;
  align-items: center;
  margin-top: 1rem;
}
.settings__container .settigns__content .settings1 .campo .info_campo img {
  border-radius: 50%;
  background-size: cover;
  width: 50px;
}
.settings__container .settigns__content .settings1 [type=file] {
  height: 0.1px;
  width: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.settings__container .settigns__content .settings1 [type=file] + label {
  margin-left: 1rem;
  position: relative;
  background-color: rgba(255, 255, 255, 0.315);
  padding: 1rem 3rem;
  color: #FFFFFF;
  box-shadow: 1px 1px 30px -5px #5880f6;
  margin-top: 2rem;
  font-size: 1.8rem;
  border-radius: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition: all 0.15s ease-in-out;
  text-align: center;
  text-decoration: none;
  letter-spacing: 1px;
  overflow: hidden;
  margin-top: 0;
  cursor: pointer;
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 700;
  overflow: hidden;
  padding: 0.625rem 1.25rem;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.settings__container .settigns__content .settings1 [type=file] + label span {
  position: relative;
  z-index: 2;
}
.settings__container .settigns__content .settings1 [type=file] + label::before {
  content: "";
  position: absolute;
  top: var(--y);
  left: var(--x);
  transform: translate(-50%, -50%);
  width: 0px;
  height: 0px;
  border-radius: 50px;
  background: #412EC2;
  z-index: 0;
  transition: all 0.7s;
}
.settings__container .settigns__content .settings1 [type=file] + label:hover {
  box-shadow: 0 0px 5px rgba(255, 255, 255, 0.7) !important;
}
@media (min-width: 768px) {
  .settings__container .settigns__content .settings1 [type=file] + label {
    width: auto;
  }
}
.settings__container .settigns__content .settings1 [type=file] + label:hover {
  transition: width 0.15s, height 0.15s;
  box-shadow: 1px 1px 30px -5px #449de6;
}
.settings__container .settigns__content .settings1 [type=file] + label:hover::before {
  transition: all 0.7s;
  width: 400px;
  height: 300px;
  border-radius: 50%;
}